 <template>
    <div class="myOrder">
        <van-nav-bar fixed title="" left-text="" left-arrow @click-left="linkBack" />
        <van-tabs
            v-model="active"
            class="tab_bar"
            title-active-color="rgb(190,157,83)"
            color="rgb(190,157,83)"
            @click="clickTopTab"
        >
            <van-tab
                v-for="(item) in tabsConfig"
                :key="item.name"
                :title="item.title"
                :name="item.name"
            ></van-tab>
        </van-tabs>

        <van-list
            class="data"
            v-model="listLoading"
            :finished="finished"
            finished-text=""
            @load="onLoad"
        >
            <van-cell class="li" v-for="item in orderData" :key="item.id">
                <section class="item">
                    <img class="item_main_img" :src="item.detaileList[0].mainImg" />
                    <section class="container">
                        <p class="order_money">{{ item.amount}}</p>
                        <p class="order_number">{{ item.id }}</p>
                        <p class="time">{{ item.DateTimePay }}</p>
                    </section>
                </section>
                <footer
                    class="get_cargo_address"
                >{{ item.GetProductAddress }} {{item.consignee}}</footer>
                <van-button
                    v-if="item.States == 15"
                    :listLoading="btnLoading && item.id == activeId"
                    type="info"
                    size="mini"
                    class="get_cargo_btn"
                    @click="confirmGetCargo(item.id)"
                ></van-button>
            </van-cell>
        </van-list>
    </div>
</template>

<script>
import { Toast } from "vant";
import { reqOrderData, reqGetCargo } from "@/api/myOrder";

/*  */
const tabsConfig = [
    { title: "", name: 0 },
    { title: "", name: 1 },
    { title: "", name: 2 },
    { title: "", name: 3 }
];
const pagesize = 10;
// ()
let getOrderDataParmas = [
    { pageindex: 1, pagesize, status: -1 }, // 
    { pageindex: 1, pagesize, status: 10 }, // 
    { pageindex: 1, pagesize, status: 15 }, // 
    { pageindex: 1, pagesize, status: 20 } // ()
];
export default {
    name: "myOrder",
    data() {
        return {
            active: 0, //  
            tabsConfig, // 
            getOrderDataParmas, // 
            orderData: [], // 
            orderDataTotal: 1, // 
            listLoading: false, // 
            finished: false, // 
            btnLoading: false, //
            activeId: -1 // 
        };
    },
    created() {
        if(this.$route.query.initId) {
            this.active = parseInt(this.$route.query.initId);
        }
        this.getOrderData(this.getOrderDataParmas[this.active]);
    },
    methods: {
        onLoad() {
            // 
            if (this.getOrderDataParmas[this.active].pageindex > this.orderDataTotal) {
                this.finished = true;
                return;
            }

            //  
            this.getOrderDataParmas[this.active].pageindex += pagesize;
            this.getOrderData(this.getOrderDataParmas[this.active]);
            console.count(" >>>>");
        },

        /*   */
        getOrderData(params) {
            reqOrderData(params).then(res => {
                if (res.data.status === 0) {
                    if (this.orderData.length <= 0) {
                        this.orderData = res.data.data.list;
                        this.orderDataTotal = res.data.data.recordcount;
                    } else {
                        res.data.data.list.forEach(item => {
                            this.orderData.push(item);
                        });
                    }
                    // 
                    this.listLoading = false;
                } else {
                    console.error(":" + res.data.msg);
                }
            });
        },

        /* tab */
        clickTopTab(active) {
            this.active = active;
            //   
            this.orderData = [];
            // 
            this.listLoading = true;
            // 
            this.getOrderData(this.getOrderDataParmas[this.active]);
            console.log(" ==>>" + active);
        },
        
        /*  */
        confirmGetCargo(oid) {
            // 
            if(this.btnLoading && this.activeId == oid) {
                return;
            }
            this.activeId = oid;
            this.btnLoading = true;
            reqGetCargo(oid).then(res => {
                if (res.data.status === 0) {
                    // 
                    this.orderData = [];
                    // 
                    this.getOrderDataParmas[this.active].pageindex = 1;
                    this.getOrderData(this.getOrderDataParmas[this.active]);
                    Toast.success(res.data.msg);
                    this.btnLoading = false;
                } else {
                    Toast.fail(res.data.msg);
                }
            });
        },

        /*  */
        linkBack() {
            this.$router.back();
        }
    }
};
</script>

<style lang="less" scoped>
.myOrder {
    padding-top: 90px;
    box-sizing: border-box;
    background-color: rgb(240, 239, 245);
    .tab_bar {
        position: fixed;
        top: 45px;
        width: 100%;
        z-index: 1;
    }
    .data {
        display: flex;
        flex-direction: column;
        padding: 10px;
        .li {
            padding: 0;
            background-color: #fff;
            margin-bottom: 10px;
            border-radius: 5px;
            position: relative;
            .item {
                height: 100px;
                display: flex;
                align-items: center;
                .item_main_img {
                    width: 80px;
                    height: 80px;
                    margin: 0 20px;
                    border-radius: 5px;
                }
                .container {
                    height: 100%;
                    flex: 3.5;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    color: rgb(120, 120, 120);
                    font-size: 14px;
                    .order_money {
                        font-weight: normal;
                    }
                    .order_number {
                        margin: 10px 0;
                    }
                    .time {
                        font-size: 14px;
                    }
                }
            }
            .get_cargo_address {
                margin: 0 20px 10px 20px;
                color: rgb(120, 120, 120);
                font-size: 12px;
            }
            .get_cargo_btn {
                position: absolute;
                right: 10px;
                top: 10px;
            }
        }
    }
}
// border: 1px solid #0ff;
</style>


